import {
  CognitoUserPool,
  CognitoUserAttribute,
  CognitoUser,
  AuthenticationDetails,
} from "amazon-cognito-identity-js";
import jwtDecode from "jwt-decode";

const awsConfig = {
  UserPoolId: "ap-south-1_v6XcHWJPJ", // Your user pool id here
  ClientId: "47tmdgu75sn6emsprohqusa1ko", // Your client id here
};

const userPool = new CognitoUserPool(awsConfig);
export function signIn(email, passWord) {
  var userData = {
    Username: email,
    Pool: userPool,
  };
  var cognitoUser = new CognitoUser(userData);

  var authenticationData = {
    Username: email,
    Password: passWord,
  };
  var authenticationDetails = new AuthenticationDetails(authenticationData);

  cognitoUser.authenticateUser(authenticationDetails, {
    onSuccess: function (result) {
      var accessToken = result.getAccessToken().getJwtToken();
      var id_Token = result.getIdToken();
      console.log(id_Token);
      //      console.log(decodeJWTToken(accessToken));
    },
    onFailure: function (error) {
      debugger;
      console.log(error);
    },
  });
}
