import React, { useState } from "react";
import { signIn } from "../Auth/cognito-user";

export function SignIn() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  function handleOnClick() {
    console.log(`email:${email} password:${password}`);
    signIn(email, password);
  }

  return (
    <div className="min-h-screen flex justify-center items-center">
      <div className="w-[500px] bg-white rounded-xl flex flex-col p-12">
        <div>
          <h1 className="text-black text-3xl font-medium mb-4 text-center">
            Welcome back !
          </h1>

          <form>
            <input
              type="text"
              onChange={(e) => setEmail(e.target.value)}
              className="w-full text-black border bg-gray-200 rounded-md p-5 focus:border-blue-500 mb-3"
              placeholder="Email"
            />
            <input
              type="text"
              onChange={(e) => setPassword(e.target.value)}
              className="w-full border bg-gray-200 text-black rounded-md p-5 focus:border-blue-500 mb-3"
              placeholder="Password"
            />

            <input
              type="button"
              onClick={handleOnClick}
              className="w-full border bg-blue-200 text-white text-base font-bold rounded-md p-5 mb-2 cursor-pointer"
              value="Sign In"
            />
          </form>
          <div className="text-center mb-3">
            <span className="text-blue-500 cursor-pointer">
              Forgot your Password?
            </span>
          </div>
          <div className="mb-3">
            <hr className="border border-b-slate-400" />
          </div>
          <div className="text-center">
            <span className="text-black">Don't have account?</span>
            <span className=" text-blue-500 cursor-pointer border-b-[3px] border-b-orange-400  pb-[3px]">
              Sign Up
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}
